import React from "react";
import logo from "../../assets/img/IOX_LOGO.png";
import twitterIcon from "../../assets/img/twitter.png";
import telegramIcon from "../../assets/img/telegram.png";
import discordIcon from "../../assets/img/discord.png";
const HomeFooter = ()=>{
    return (
        <footer className="footer">
            <div className="container">
                <div className="d-flex">
                    <div className="col-lg-4">
                        <a href="" className="logo">
                            <img className="logo__img" src={logo} alt=""/>
                        </a>
                        <div className="copyright">© 2022, IOXStaking</div>
                    </div>
                    <div className="col-lg-4">
                        <div className="social-block">
                            <div className="social-block__title">
                                Stay connected:
                            </div>
                            <ul className="social-list">
                                <li className="social-list__item">
                                    <a href="#" className="social-list__link">
                                        <img src={twitterIcon} className="icon" />              
                                    </a>
                                </li>
                                <li className="social-list__item">
                                    <a href="#" className="social-list__link">
                                        <img src={telegramIcon} className="icon" />
                                    </a>
                                </li>
                                <li className="social-list__item">
                                    <a href="#" className="social-list__link" style={{textAlign: "center",
                                        paddingTop: '6px'}}>
                                        <img src={discordIcon} className="icon" />
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-lg-4">
                        <div className="social-block">
                            <div className="social-block__title">
                                Contact us
                            </div>
                            <span>info@ioxstaking.net</span>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default HomeFooter;