import moment from "moment";

import React, { useEffect, useState } from 'react';
import Web3 from 'web3';

import { ellipsisAddr } from "../utils/functions";

window.web3 = new Web3(window.ethereum);
console.log(window.web3)

export default function EventBar({ account, contract }) {

  const [recentEvents, setRecentEvents] = useState([]);

  useEffect(async () => {
    getEvents();
  }, [account, contract])

  const getEvents = async () => {
    if (!contract) return;
    let lastEvents = [];
    let blockNumber = await window.web3.eth.getBlockNumber();
    // console.log(blockNumber);
    for (; blockNumber > 21165104; blockNumber = blockNumber - 5001) {
      const fromBlock = (blockNumber - 5000) < 0 ? 0 : (blockNumber - 5000);
      const _events = await contract.getPastEvents('AllEvents', {
        fromBlock: fromBlock,
        toBlock: blockNumber,
      });
      _events.reverse();
      // console.log(_events);
      _events.map((_event) => {
        if (lastEvents.length > 19) return;
        if (_event.event === "UserLobby") {
          const _type = 1;
          const _address = _event.returnValues.addr;
          const _text = `Auction Entered: ${_event.returnValues.rawAmount / 1e18} BNB`;
          const _time = moment.unix(_event.returnValues.timestamp).fromNow();
          const _link = `https://testnet.bscscan.com/tx/${_event.transactionHash}`;
          // console.log({address: _address, text: _text, time: _time, link: _link})
          lastEvents.push({ type: _type, address: _address, text: _text, time: _time, link: _link });
        }
        if (_event.event === "UserStakeCollect") {
          const _type = 2;
          const _address = _event.returnValues.addr;
          const _text = `Stake Collected: ${_event.returnValues.rawAmount / 1e18} BNB`;
          const _time = moment.unix(_event.returnValues.timestamp).fromNow();
          const _link = `https://testnet.bscscan.com/tx/${_event.transactionHash}`;
          // console.log({address: _address, text: _text, time: _time, link: _link})
          lastEvents.push({ type: _type, address: _address, text: _text, time: _time, link: _link });
        }
        if (_event.event === "stake_sell_request") {
          const _type = 3;
          const _address = _event.returnValues.addr;
          const _text = `${_event.returnValues.rawAmount / 1e18} SEED Stake sell offer for ${_event.returnValues.price / 1e18} BNB`;
          const _time = moment.unix(_event.returnValues.timestamp).fromNow();
          const _link = `https://testnet.bscscan.com/tx/${_event.transactionHash}`;
          // console.log({address: _address, text: _text, time: _time, link: _link})
          lastEvents.push({ type: _type, address: _address, text: _text, time: _time, link: _link });
        }
      })
      if (lastEvents.length > 19) break;
      // console.log(blockNumber)
    }
    setRecentEvents(lastEvents);
    setTimeout(() => {
      getEvents();
    }, 30000);
  }

  return (
    <div className="event-bar">
      <div className="title">
        Recent Events
      </div>
      <div className="event-list">
        {
          recentEvents?.map((_event, index) =>
            <a href={_event.link} target="_blank" className={`event-box-${_event.type}`} key={index}>
              <div className="address">{ellipsisAddr(_event.address)}</div>
              <div className="text">{_event.text}</div>
              <div className="time">{_event.time}</div>
            </a>
          )
        }
      </div>
    </div>
  )
}
