import React from "react";
import './introduce.scss';
import logo from "../../assets/img/IOX_LOGO.png";
import { Link } from "react-router-dom";

const Introduce = ()=>{
    return (
        <div className="introduce">
            <div className="container">
                <div className="d-flex">
                    <div>
                        <h2 className="title">What is IOXStaking</h2>
                        <p className="desc">
                            At its core, Avarice means greed. It is an incredibly lucrative staking DApp that is built on the shoulders of giants. Drawing inspiration from the highly successful T2X platform from the glory days of DeFi; Avarice brings the auction fun and FOMO to the Binance Smart Chain.
                        </p>
                        <div className="d-flex mt-20">
                            <Link to="/lobby" className="btn btn--orange">Get SEED Token</Link>
                            <a href="#" className="btn btn--blue">Get SEED Token</a>
                        </div>
                    </div>
                    <div className="logo">
                        <img src={logo}></img>
                    </div>
                </div>
                
            </div>
        </div>
    )    
}

export default Introduce;